<template>
  <div ref="content">
    <b-row
      @click="updateToggle"
      class="w-full justify-content-between align-items-center my-4 mx-auto"
    >
      <b-col cols="12" xl="12">
        <span class="font-weight-bold judgmentVal-font"
          >{{ financialYear }}{{ $t("YEARJP") }}{{ month }}{{ $t("MONTHJP") }}
          {{ $t("DASHBOARD.FOR_COMPANY_JUDGEMENT") }}
        </span>
        <b-button v-if="companyPosition" class="ml-2 performanceButton">
          {{ rankLetters[companyPosition - 1] }}
        </b-button>
        <span class="ml-2 text-dashboard d-none d-md-none d-sm-none d-lg-flex">
          {{ $t("DASHBOARDPERFORMER") }}</span
        >
        <button
          @click="goToInputExpenseItem"
          class="btn btn-custom-color margin-right-4 float-right"
        >
          {{ $t("GRAPH.ENTER") }}
        </button>
      </b-col>
    </b-row>
    <b-row class="justify-content-center" >
      <b-col cols="12" xl="4" md="8" class="mb-4">
        <button
          id="toggleBtn"
          @click.prevent="toggleSubtree = !toggleSubtree"
          class="w-100 px-3 py-2 dropdownToggle border-0 d-flex justify-content-between align-items-center"
        >
          <span class="title-dropdown">{{$t("SELECTDROPDOWN")}}</span>
          <span
            ><i
              :class="toggleSubtree ? 'fa fa-caret-down' : 'fa fa-caret-down'"
            ></i
          ></span>
        </button>
        <b-row
          id="dashboardAccountDropdown"
          v-if="toggleSubtree"
          class="dropdownToggleBody mx-auto"
        >
          <b-col cols="12" xl="12" class="selectExpenseItem">
            <b-col class="submenuScroll">
            <SubMenuTree
              :tree-options="treeOptions"
              @update-multiselect-exp-item="getExpItmCodeArray"
              :treeTitle="treeTitle"
              :selectedState="selectedState"
              class="mt-1"
            />
            </b-col>
            <div class="w-100 mb-2 mt-2 px-2 d-flex align-items-center justify-content-center">
              <span>
                <b-button
                  @click.prevent="applyCheckExpItem"
                  variant="primary"
                  class="py-1 modal-mblBtn modal-updateBtn ml-1"
                  >{{ $t("UPDATE") }}</b-button
                >
              </span>
              <span>
                <b-button
                  @click.prevent="clearSubmenu()"
                  class="modal-clearButton ml-3 modal-mblBtn py-1"
                  >{{ $t("CLEAR") }}</b-button
                >
              </span>
            </div>
          </b-col>
        </b-row>
      </b-col>
    </b-row>
    <b-row :no-gutters="true" @click="updateToggle" class="align-items-start">
      <b-col cols="12" md="9">
      </b-col>
      <b-col cols="12" md="3"></b-col>
      <b-col cols="12" md="12">
        <div v-if="loading" class="text-center mb-2">
          <strong class="mx-2">Loading...</strong>
          <b-spinner class="ml-auto"></b-spinner>
        </div>
        <div id="arc1"></div>
      </b-col>
    </b-row>
    <b-row @click="updateToggle">
      <b-col cols="12" class="d-flex justify-content-center align-items-center">
        <div
          class="py-2 px-2 pillButtons bg-white d-flex justify-content-center align-items-center"
        >
          <b-link
            :disabled="prevLinkDisabled"
            @click="getPrevMonth()"
            href="javascript:;"
            class="link d-flex justify-content-center align-items-center"
          >
            <span>
              <i
                class="fa fa-caret-left fa-2x text-muted mr-2"
                aria-hidden="true"
              ></i>
            </span>
            <span class="mr-2">{{ $t("PREVIOUSMONTH") }}</span>
          </b-link>
          &nbsp;|
          <b-link
            :disabled="nextLinkDisabled"
            @click="getNextMonth()"
            href="javascript:;"
            class="link ml-2 d-flex justify-content-center align-items-center"
          >
            <span>{{ $t("NEXTMONTH") }}</span>
            <span>
              <i
                class="fa fa-caret-right fa-2x text-muted ml-2"
                aria-hidden="true"
              ></i>
            </span>
          </b-link>
        </div>
      </b-col>
    </b-row>
    <b-row @click="updateToggle" class="mt-3">
      <b-col
        cols="12"
        class="d-none d-sm-none d-md-none d-lg-flex justify-content-center align-items-center mb-5"
      >
        <b-button
          id="explanationButton"
          v-b-tooltip.hover.bottom.html="slimToolTip"
          v-b-tooltip.hover ="{id:'tooltip2',placement: 'bottom' }"
          variant="primary"
          class="explanationButton"
          >{{ $t("OVERALLJUDGEMENTCHART.SLIM") }}</b-button
        >
        <b-button
          id="explanationButton"
          v-b-tooltip.hover.bottom.html="healthyToolTip"
          v-b-tooltip.hover ="{id:'tooltip2',placement: 'bottom' }"
          variant="primary"
          class="explanationButton"
          >{{ $t("OVERALLJUDGEMENTCHART.HEALTHY") }}</b-button
        >
        <b-button
          v-b-tooltip.hover.bottom.html="fatToolTip"
          v-b-tooltip.hover ="{id:'tooltip2',placement: 'bottom' }"
          variant="primary"
          class="explanationButton"
          >{{ $t("OVERALLJUDGEMENTCHART.FAT") }}</b-button
        >
        <b-button
          id="explanationButton"
          v-b-tooltip.hover.bottom.html="metaboToolTip"
          v-b-tooltip.hover ="{id:'tooltip2',placement: 'bottom' }"
          variant="primary"
          class="explanationButton"
          >{{ $t("OVERALLJUDGEMENTCHART.METABO") }}</b-button
        >
        <b-button
          id="explanationButton"
          v-b-tooltip.hover.bottom.html="spoilerToolTip"
          v-b-tooltip.hover ="{id:'tooltip2',placement: 'bottom' }"
          variant="primary"
          class="explanationButton"
          >{{ $t("OVERALLJUDGEMENTCHART.SPOILER") }}</b-button
        >
      </b-col>
    </b-row>
    <b-row @click="updateToggle" class="d-md-block d-lg-none px-4">
      <b-col cols="12" class="w-100 mt-3 justify-content-center text-center">
        <b-button class="expandButton">
          <img
            src="@/assets/hatena-icon.svg"
            class="navbar-brand-full p-1"
            width="10%"
            alt="Expense Reduction Management Logo"
          />
          コストについての説明
          <span @click.prevent="showFirst = !showFirst">
            <span v-if="showFirst">
              <i
                class="fa fa-minus mt-1 text-center float-right"
                aria-hidden="true"
              ></i>
            </span>
            <span v-if="!showFirst">
              <i
                class="fa fa-plus mt-1 text-center ipadBtn float-right"
                aria-hidden="true"
              ></i>
            </span>
          </span>
        </b-button>
      </b-col>
      <b-col v-if="showFirst" cols="12" sm="3" md="12">
        <ul class="list-unstyled mt-3 list-legend ml-1">
          <li
            v-for="(legend, index) in $t('GRAPH.LEGENDS')"
            :key="index"
            class="d-flex justify-content-start mb-3"
          >
            <div>
              <span
                :style="`color:${legend.COLOR}`"
                class="block py-1 mr-1 font-weight-bold"
              >
                {{ legend.LEGEND }}
              </span>
              <br />
              <span class="block py-1">{{ legend.MEANING }}</span>
            </div>
          </li>
        </ul>
      </b-col>
    </b-row>

    <b-modal
      id="modal-tall"
      centered
      variant="secondary"
      modal-class="py-0"
      body-class="py-0"
      size="md"
      footer-class="border-0"
      header-class="border-0 bg-white"
    >
      <template v-slot:modal-header="{ cancel }">
        <div class="w-100 d-flex justify-content-end">
          <button
            @click="cancel()"
            class="cross-close-btn border-dark text-dark font-weight-bold text-sm m-0 bg-white mr-2"
          >
            X
          </button>
        </div>
      </template>
      <b-card class="pop-up-box">
        <div class="my-0 py-0 px-2">
          <ul class="list-unstyled my-0 py-0">
            <li
              v-for="(OVERALL_JUDGEMENT, index) in $t(
                'DASHBOARD.OVERALL_JUDGEMENTS'
              )"
              :key="index"
            >
              <span class="font-weight-bold block">
                {{ OVERALL_JUDGEMENT.LABEL }}
              </span>
              <p class="mb-0">{{ OVERALL_JUDGEMENT.VALUE }}</p>
            </li>
          </ul>
        </div>
      </b-card>
      <template v-slot:modal-footer="{ cancel }">
        <div class="w-100">
          <button
            @click="cancel()"
            class="btn-sm float-left btn-custom-color px-1 ml-2"
          >
            {{ $t("CLOSE") }}
          </button>
        </div>
      </template>
    </b-modal>
    <b-modal
      id="tree-modal"
      ref="tree-modal"
      :hide-footer="false"
      centered
      variant="secondary"
      modal-class="py-0"
      body-class="border-0 py-0"
      size="md"
      footer-class="border-0"
      header-class="border-0 bg-white pd-0"
      button-size="sm"
      hide-header-close
    >
      <template v-slot:modal-header="{ cancel }">
        <div class="title-fs font-weight-bold mt-2 text-dark ml-3">
          {{ $t("EXPENSEITEMSELECTION") }}
        </div>
        <b-button
          @click="cancel"
          class="back-btn close-btn mt-2"
          variant="primary"
          >{{ $t("CLOSE") }}</b-button
        >
      </template>
      <b-card class="pop-up-box">
        <b-row>
          <b-col class="overflow-subtree">
            <SubMenuTree
              :tree-options="treeOptions"
              :treeTitle="treeTitle"
              @update-multiselect-exp-item="getExpItmCodeArray"
            />
          </b-col>
        </b-row>
      </b-card>
      <template #modal-footer>
        <div class="w-100">
          <b-button
            @click.prevent="applyCheckExpItem"
            variant="primary"
            type="submit"
            class="px-4 send-btn float-right mr-2"
            >{{ $t("SELECTEXPENSEITEMS") }}</b-button
          >
        </div>
      </template>
    </b-modal>
  </div>
</template>

<script>
import * as d3 from "d3";
/** Importing the image  */
import backgroundImage from "../../../public/matrix-bg_pc.png";
import SubMenuTree from "@/components/SubMenuTreeCheckboxDashboard.vue";
import api from "@/shared/api";
import i18n from "@/plugins/i18n";
import { mapGetters } from "vuex";
import { ApiServicesFactory } from "@/shared/apiServiceFactory";
const BasicInfoApis = ApiServicesFactory.get("basic");
import { endPoint } from "@/shared/endPoint";

export default {
  components: { SubMenuTree },
  data() {
    return {
      treeTitle: " ",
      loading: false,
      showFirst: false,
      selectedState: false,
      toggleSubtree: false,
      nextLinkDisabled: false,
      prevLinkDisabled: false,
      settingLinkDisabled: false,
      dataApi: [],
      settingLinkValue: "",
      companySettingsArray: {
        industryNumber: "",
        industryType: "",
        floorSpace: "",
        capital: "",
        revenue: "",
        employees: "",
      },
      treeOptions: {
        checkbox: true,
      },
      month: "",
      year: "",
      companyPosition: "",
      /** TODO: Read the api data */
      dataset: [],
      checkedExpenseItemCodeArray: [],
      datasetApi: [],
      /**  Assign the imported image to a data varibale, so we can bind the image anywhere in this page like a varibale */
      bgImage: backgroundImage,
      costInfoTree: [],
      yearStartMonth: "",
      financialYear: new Date().getFullYear(),
      rankLetters: ["S", "A", "B", "C", "D", "E"],
    };
  },
  computed: {
    ...mapGetters({
      checkExpenseItemList: "getCheckedExpItemCodeArrayInDashboard",
      userAccountData: "getUserDetails",
    }),

    getData() {
      return this.dataset;
    },
  },
  created() {
    this.getUnreadNotifications();
  },

  async mounted() {
    this.$store.dispatch("updateExpenseItemYear", {
      expense_item_year: "",
    });
    this.getTreeData();
  },

  methods: {
    /**Function to fetch financial year start month for logged in client account */
    async getYearStartMonth() {
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERREADDATAMCLIENTOTHERINFO,
        {
          fieldlist: ["client_no", "input_agency_option", "year_start_month"],
          conditions: [
            {
              key: "client_no",
              value: localStorage.getItem("client_account_no"),
            },
          ],
        }
      );
      let responseData = data.data;
      this.yearStartMonth = responseData[0].year_start_month
        ? responseData[0].year_start_month
        : 1;
      if (this.yearStartMonth > parseInt(this.getApiMonth)) {
        this.financialYear = parseInt(this.getApiYear) - 1;
      } else {
        this.financialYear = parseInt(this.getApiYear);
      }
    },

    /**Function to fetch tree data for ExpenseItem available for logged in client account */
    async getTreeData() {
      this.loading = true;
      const { data } = await BasicInfoApis.postAPI(endPoint.DASHBOARD, {
        client_no: localStorage.getItem("client_account_no"),
      });
      this.loading = false;
      if (data.success) {
        let responseData = data.data.clientJudgementArray;
        this.costInfoTree = responseData;
        // this.checkedExpenseItemCodeArray = this.costInfoTree.map(
        //   (Item) => Item.expense_item_cd
        // );
        this.costInfoTree.forEach(item => {
          let expenseData = {
            expense_item_cd : item.expense_item_cd,
            accounts_name_cd : item.accounts_name_cd
          }
          this.checkedExpenseItemCodeArray.push(expenseData);
        });
        if (this.checkExpenseItemList.length > 0) {
          this.checkedExpenseItemCodeArray = this.checkExpenseItemList;
        }
        this.$store.dispatch(
          "updateCheckedExpItemCodeArrayInDasboard",
          this.checkedExpenseItemCodeArray
        );
        /** Call the draw method here to render the svg when the page is loaded */
        await this.dashboardApi();
        this.draw();
        this.constructTreeArray();
      } else {
        await this.dashboardApi();
        this.draw();
      }
    },
    clearSubmenu() {
      this.checkedExpenseItemCodeArray = [];
      this.$store.dispatch(
        "updateCheckedExpItemCodeArrayInDasboard",
        this.checkedExpenseItemCodeArray
      );
      if(this.checkedExpenseItemCodeArray.length <= 0){
        this.selectedState = true;
      }else{
        this.selectedState = false;
      }
      this.dashboardApi();
      this.constructTreeArray();
      this.toggleSubtree = false;
    },
    /**Function to re-draw the graph after selecting expenseItems from modal popup */
    applyCheckExpItem() {
      this.$store.dispatch(
        "updateCheckedExpItemCodeArrayInDasboard",
        this.checkedExpenseItemCodeArray
      );
      if(this.checkedExpenseItemCodeArray.length <= 0){
        this.selectedState = true;
      }else{
        this.selectedState = false;
      }
      this.dashboardApi();
      this.constructTreeArray();
      this.toggleSubtree = false;
      // this.$refs["tree-modal"].hide();
    },
    /**Function to compile and bind data as tree format and send to store */
    constructTreeArray() {
      let mapData = [];
      let groupedData = this._.groupBy(this.costInfoTree, "accounts_name_nm");
      Object.keys(groupedData).forEach((key) => {
        let childrenArray = [];
        groupedData[key].forEach((item) => {
          let bindChildData;
          let findObject = this.checkExpenseItemList.find(element => element.expense_item_cd === item.expense_item_cd && element.accounts_name_cd === item.accounts_name_cd);
          if (findObject) {
            bindChildData = {
              text: item.expense_item_nm,
              state: { checked: true },
              expenseItemCode: item.expense_item_cd,
              accountNameCode: item.accounts_name_cd,
            };
          } else {
            bindChildData = {
              text: item.expense_item_nm,
              expenseItemCode: item.expense_item_cd,
              accountNameCode: item.accounts_name_cd,
            };
          }
          childrenArray.push(bindChildData);
        });
        let bindTreeData = {
          text: key,
          state: { expanded: true },
          children: childrenArray,
        };
        mapData.push(bindTreeData);
      });
      this.$store.dispatch("updateTreeDashboard", mapData);
    },

    /**Function to re-draw the graph based on previous month data */
    async getPrevMonth() {
      this.nextLinkDisabled = false;
      this.month = parseInt(this.month) - 1;
      if (this.yearStartMonth > parseInt(this.month)) {
        this.financialYear = parseInt(this.year) - 1;
        if (this.month <= 0) {
          this.year = parseInt(this.year) - 1;
          this.month = 12;
        } else if (this.month > 9) {
          this.month = this.month;
        } else {
          this.month = "0" + this.month;
        }
      } else {
        if (this.month <= 0) {
          this.year = parseInt(this.year) - 1;
          this.month = 12;
        } else if (this.month > 9) {
          this.month = this.month;
        } else {
          this.month = "0" + this.month;
        }
      }
      await this.dashboardApi();
      await this.draw();
    },

    /**Function to re-draw the graph based on next month data if available */
    async getNextMonth() {
      if (
        parseInt(this.getApiMonth) <= parseInt(this.month) + 1 &&
        parseInt(this.getApiYear) == this.year
      ) {
        this.nextLinkDisabled = true;
      }
      this.month = parseInt(this.month) + 1;
      if (this.month > 12) {
        this.year = parseInt(this.year) + 1;
        this.month = "01";
      } else if (this.month > 9) {
        this.month = this.month;
      } else {
        this.month = "0" + this.month;
      }
      if (this.yearStartMonth > parseInt(this.month)) {
        this.financialYear = parseInt(this.year) - 1;
      } else {
        this.financialYear = parseInt(this.year);
      }
      await this.dashboardApi();
      await this.draw();
    },

    /**Function to fetch data for graph draw and compiling data and given to drawing function */
    async dashboardApi() {
      this.loading = true;
      if (this.month == "") {
        this.dataApi = await api.post(endPoint.DASHBOARD, {
          client_no: localStorage.getItem("client_account_no"),
          month: this.month,
          year: this.year,
          expense_item_cd: this.checkedExpenseItemCodeArray,
        });

        if (this.dataApi.data.success) {
          this.getApiMonth = this.dataApi.data.data.dataAvailableMonthYear
            .toString()
            .slice(4, 6);
          this.getApiYear = this.dataApi.data.data.dataAvailableMonthYear
            .toString()
            .slice(0, 4);
          this.nextLinkDisabled = true;
          let dataExists = this.dataApi.data.data.clientJudgementArray;
          if (dataExists.length == 0) {
            this.prevLinkDisabled = true;
          }
          this.getYearStartMonth();
        } else {
          this.getApiMonth = new Date().getMonth() + 1;
          this.getApiYear = new Date().getFullYear();
          this.nextLinkDisabled = true;
          this.prevLinkDisabled = true;
          this.getYearStartMonth();
        }
      } else {
        this.dataApi = await api.post(endPoint.DASHBOARD, {
          client_no: localStorage.getItem("client_account_no"),
          month: this.month,
          year: this.year,
          expense_item_cd: this.checkedExpenseItemCodeArray,
        });
      }
      if (!this.dataApi.data.success) {
        this.loading = false;
        this.settingLinkDisabled = true;
        this.nextLinkDisabled = true;
        this.prevLinkDisabled = true;
        let currentMonth = new Date().getMonth() + 1;
        if (currentMonth > 9) {
          this.month = currentMonth;
        }
        if (currentMonth < 10) {
          this.month = "0" + currentMonth;
        }
        this.year = new Date().getFullYear();
      }
      if (this.dataApi.data.success) {
        this.month = this.dataApi.data.data.dataAvailableMonthYear
          .toString()
          .slice(4, 6);
        this.year = this.dataApi.data.data.dataAvailableMonthYear
          .toString()
          .slice(0, 4);

        let settingCheck = this.dataApi.data.data.companySettingArray;
        if (settingCheck) {
          this.companySettingsArray = {
            industryType: settingCheck.industry_nm
              ? settingCheck.industry_nm
              : "",
            floorSpace: settingCheck.floor_space
              ? parseInt(settingCheck.floor_space)
              : "",
            capital: settingCheck.capital ? settingCheck.capital : "",
            revenue: settingCheck.revenue ? settingCheck.revenue : "",
            employees: settingCheck.employee_number
              ? settingCheck.employee_number
              : "",
          };
        }

        this.generateSettingLinkValueHtml();
        this.datasetApi = this.dataApi.data.data.clientJudgementArray;
        if (this.dataApi.data.data.judgementCompanyDetails.length > 0) {
          localStorage.setItem(
            "judg_company_point",
            this.dataApi.data.data.judgementCompanyDetails[0].judg_company_point
          );
          this.$store.dispatch(
            "updateExcellentPoint",
            this.dataApi.data.data.judgementCompanyDetails[0]
              .judg_company_point + "P"
          );
          this.companyPosition = parseInt(
            this.dataApi.data.data.judgementCompanyDetails[0].judg_company
          );
        } else {
          this.$store.dispatch("updateExcellentPoint", "0P");
          this.companyPosition = "";
        }
        if (this.checkedExpenseItemCodeArray.length <= 0) {
          this.datasetApi = [];
        }
        this.dataset = this.datasetApi.map((data) => {
          return {
            name: data.expense_item_nm,
            x: data.cost ? (parseInt(data.cost) <= 2000000 ? parseInt(data.cost) : 2000000) : "" ,
            y: data.reduction_range ? (parseInt(data.reduction_range) >= 70 ? (parseInt(data.reduction_range) <=230 ? parseInt(data.reduction_range) : 230) : 70) : "",
            accounts_name_cd: data.accounts_name_cd ? data.accounts_name_cd : "",
            expense_item_cd: data.expense_item_cd ? data.expense_item_cd : "",
          };
        });
        //this.constructHoverData();
        this.assignColorCode();
        this.draw();
        this.loading = false;
      }
    },
    isMobileScreen() {
      if (
        navigator.userAgent.match(/Android/i) ||
        navigator.userAgent.match(/webOS/i) ||
        navigator.userAgent.match(/iPhone/i) ||
        navigator.userAgent.match(/iPad/i) ||
        navigator.userAgent.match(/iPod/i) ||
        navigator.userAgent.match(/BlackBerry/i) ||
        navigator.userAgent.match(/Windows Phone/i)
      ) {
        return true;
      } else {
        return false;
      }
    },

    test() {
      let arrayList = this.dataset.map((item) => {
        return {
          x: item.x,
          y: item.y,
          // code: item.expense_item_cd,
        };
      });

      let m = arrayList.map((item) => {
        if (this._.filter(arrayList, { x: item.x, y: item.y }).length > 1) {
          if (item) {
            return item;
          }
        }
      });
      let newM = [];
      m.map((item) => {
        if (!this._.find(newM, item)) {
          newM.push(item);
        }
      });
      console.log("newM", newM);
      return newM;
    },
    slimToolTip() {
      return this.$t("GRAPH.LEGENDS[" + 0 + "].MEANING");
    },
    healthyToolTip() {
      return this.$t("GRAPH.LEGENDS[" + 1 + "].MEANING");
    },
    fatToolTip() {
      return this.$t("GRAPH.LEGENDS[" + 2 + "].MEANING");
    },
    metaboToolTip() {
      return this.$t("GRAPH.LEGENDS[" + 3 + "].MEANING");
    },
    spoilerToolTip() {
      return this.$t("GRAPH.LEGENDS[" + 4 + "].MEANING");
    },
    assignColorCode() {
      let newDataset = this.dataset.map((item) => {
        if (
          (item.x <= 1000000) &&
          ((item.y >= 70) && (item.y < 149))
        ) {
          return Object.assign(item, {
            bgColor: "#3FB0F0",
            radius : 32
          });
        }
        if (
          (item.x >= 1000000) &&
          ((item.y >= 70) && (item.y < 149))
        ) {
          return Object.assign(item, {
            bgColor: "#A9D28E",
            radius : 32
          });
        }
        if (
          (item.x <= 750000)&&
          (item.y >= 149)
        ) {
          return Object.assign(item, {
            bgColor: "#FEB653",
            radius : 40
          });
        }
        if (
          ((item.x >= 750000) && (item.x < 1250000)) &&
          (item.y >= 149)
        ) {
          return Object.assign(item, {
            bgColor: "#FEAFE6",
            radius : 40
          });
        }
        if (
          (item.x >= 1250000) &&
          (item.y >= 149)
        ) {
          return Object.assign(item, {
            bgColor: "#F89C9F",
            radius : 40
          });
        }
      });
      console.log("KKKKKKKKK", newDataset);
    },
     getColorCode(dataX, dataY) {
      let overallWidth = 800;
      let overallHeight = 500;
      if (dataX < overallWidth / 2 && dataY > overallHeight / 2) {
        return "#3FB0F0";
      }
      if (dataX > overallWidth / 2 && dataY > overallHeight / 2) {
        return "#A9D28E";
      }
      if (dataX < overallWidth / 3 && dataY < overallHeight / 2) {
        return "#FEB653";
      }
      if (dataX > overallWidth / 3 && dataY < overallHeight / 2) {
        return "#FEAFE6";
      }
      if (dataX > overallWidth / 3 && dataY < overallHeight / 2) {
        return "#F89C9F";
      }
    },
    constructHoverData() {
      let newDataset;
      this.test().map((itemDup) => {
        let nameList = [];
        newDataset = this.dataset.map((item) => {
          if (item.x == itemDup.x && item.y == itemDup.y) {
            nameList.push(item.name);
            return Object.assign(item, {
              hover: true,
              hoverDetais: nameList,
            });
          } else {
            return item;
          }
        });
      });
      console.log("newDataset", newDataset);
    },

    /**Function to fetch unread message notification and update to the store */
    async getUnreadNotifications() {
      const { data } = await api.post(endPoint.MESSAGEREDORUNREDCOUNT, {
        client_no: localStorage.getItem("client_account_no"),
      });
      if (data.success) {
        this.$store.dispatch("updateUnreadMessages", {
          unread_messages: data.data.messageCount,
        });
      }
    },
    /**Function to route the screen for dashboard to settings screen */
    goToSettings() {
      this.$router.push({ name: "edit_settings" });
    },
    /**Function to route the screen for dashboard to Input expense Item screen */
    goToInputExpenseItem() {
      this.$router.push({ name: "user_input_expense_item" });
    },
    /**Function to create dynamic html content and display in a single line */
    generateSettingLinkValueHtml() {
      this.settingLinkValue =
        this.companySettingsArray.industryType +
        `,  ` +
        i18n.t("DASHBOARD.NUMBER_OF_EMPLOYEES") +
        this.companySettingsArray.employees +
        i18n.t("DASHBOARD.MAN_UNIT") +
        `,  ` +
        i18n.t("DASHBOARD.BUSINESS_AREA") +
        this.companySettingsArray.floorSpace +
        i18n.t("DASHBOARD.AREA_UNIT") +
        `,  ` +
        i18n.t("DASHBOARD.SALES_SCALE") +
        this.companySettingsArray.revenue +
        `万` +
        i18n.t("DASHBOARD.COST_UNIT");
    },
    updateToggle(){
      this.toggleSubtree = false;
    },
    /**Function to fetch selected expense Item from modal popup */
    getExpItmCodeArray(item) {
      this.checkedExpenseItemCodeArray = item;
    },
    draw() {
      /** TODO: Need to check diffrent screen width, need to make it dynamic  */
      const margin = { top: 50, right: 40, bottom: 50, left: 80 },
        /**  Taking the width of SVG area reducing the margin so we get the innerWidth , same as for the height   */
        /** TODO : WINDOW height and width */
        width = 879 - margin.left - margin.right,
        height = 785 - margin.top - margin.bottom;

      /**  The number of datapoints
       * TODO : need to replace with the API provided data points */

      d3.select("svg").remove();
      /**  Y scale & X scale,  We use it to map the dataset value to SVG area , like taking small ratio of bigger value along X and Y axis seperately */

      const xScale = d3
        .scaleLinear()
        .domain([0, 2000000])
        .range([0, width - 20]);

      const yScale = d3
        .scaleLinear()
        .domain([70, 230])
        .range([height - 255, 25]);

      /** Createing SVG area inside which we draw all the D3 js graph elements like ellipse , text etc */
      const svg = d3
        .select("#arc1")
        .append("svg")
        .attr("viewBox", "0 0 840 580")
        .append("g")
        .attr("transform", "translate(" + 50 + "," + 20 + ")");
      /** Add the background image */
      svg.append("image").attr("xlink:href", this.bgImage).attr("width", width).attr("height",490);
      let self = this;
      /**  Appends group element */
      const svgGroup = svg
        .selectAll(".dot")
        .data(this.getData)
        .enter()
        .append("g");
      var div = d3
        .select("body")
        .append("div")
        .attr("class", "tooltip")
        .style("opacity", 0);
      /**  Draw the ellipses in the SVG area */
      svgGroup
        .append("circle")
        .attr("class", "dot")
        .attr("cx", function (d) {
          console.log("XXXXXX",xScale(1),xScale(d.x),d.name,d.x);
          return xScale(d.x);
        })
        .attr("cy", function (d) {
          console.log("YYYYYYY",yScale(1), yScale(d.y), d.name, d.y);
          return yScale(d.y);
        })
        .attr("r",function (d) {
          return d.radius;
        })
        .style("fill", function (d) {
          return d.bgColor;
        })
        .attr("stroke", "#ffffff")
        .attr("filter", function (d) {
          return "drop-shadow(0px 0px 10px " + d.bgColor + ")";
        })
        .on("mouseover", function () {
          // if(d.hover && d.hover == true){
          //   div.transition().duration(200).style("opacity", 0.9);
          // div
          //   .html(JSON.stringify(d) + `<div class="arrow-down ml-4 "></div>`)
          //   .style("left", d3.event.pageX - 30 + "px")
          //   .style("top", d3.event.pageY - 70 + "px")
          //   .style("font-weight", "900");
          // }
        })
        .on("mouseout", function () {
          div.transition().duration(500).style("opacity", 0);
        });

      svgGroup
        .append("text")
        .attr("x", (d) => xScale(d.x) - 40)
        .attr("y", (d) => yScale(d.y) - 15)
      /** Assign the click event to the ellipse*/
      svgGroup.selectAll("ellipse").on("click", () => {
        this.$store.dispatch("updateExpenseItemYear", {
          expense_item_year: this.year,
        });
        this.$router.push({ name: "monthly_expense_details" });
      });

      /** We try with the TEXT in D3 but it's not allow to break the text inside the ellipse
       * So foreignObject is used and appending the html
       */
      svgGroup
        .append("foreignObject")
        .attr("x", (d) => xScale(d.x) - 40)
        .attr("y", (d) => yScale(d.y) - 15)
        .attr("width", 70)
        .attr("height", 40)
        .attr("stroke", "black")
        .attr("text-anchor", "middle")
        // .attr(
        //   "style",
        //   "text-align:center;background:white;cursor:pointer"
        // )
        .attr("style", "text-align:center")
        .attr("style","padding-left: 10px;")
        .append("xhtml:body")
        //.html((d) => d.hoverDetais ? d.hoverDetais.length : d.name)
        .html((d) => d.name)

        .on("mouseover", function (d) {
          if (d.hover && d.hover == true) {
            div.transition().duration(200).style("opacity", 0.9);
            div
              // .html(
              //   d.hoverDetais.map(item=>{
              //     `<b>` +
              //       d.hoverDetais[item] +
              //       `</b>`
              //   })
              //    + `<div class="arrow-down ml-4 "></div>`)
              // JSON.stringify(d) + `<div class="arrow-down ml-4 "></div>`)
              .style("left", d3.event.pageX - 30 + "px")
              .style("top", d3.event.pageY - 70 + "px")
              .style("font-weight", "900");
            // .attr('fill', '#ff0000');
          }
        })
        .on("mouseout", function () {
          div.transition().duration(500).style("opacity", 0);
        })
        .attr(
          "style",
          "text-align:center;font-size:14px;font-weight:800;color:white;overflow:hidden;background:transparent;cursor:pointer"
        );

      svgGroup.selectAll("foreignObject").on("click", (data) => {
        this.$store.dispatch("updateSelectedExpenseItem", {
          accounts_name_cd: data.accounts_name_cd,
          expense_item_cd: data.expense_item_cd,
          expense_item_nm: data.name,
        });
        this.$router.push({ name: "monthly_expense_details" });
        this.$store.dispatch("updateExpenseItemYear", {
          expense_item_year: this.year,
        });
      });
      /**  X axis line */

      svg
        .append("linearGradient")
        .attr("id", "line-gradient")
        .attr("gradientUnits", "userSpaceOnUse")
        .attr("x1", "0%")
        .attr("x2", "100%")
        .attr("y1", "0%")
        .attr("y2", "100%")
        .selectAll("stop")
        .data([
          { offset: "25%", color: "#B9E6FF" },
          { offset: "100%", color: "#FCB8BA" },
        ])
        .enter()
        .append("stop")
        .attr("offset", function (d) {
          return d.offset;
        })
        .attr("stop-color", function (d) {
          return d.color;
        });

      svg
        .append("linearGradient")
        .attr("id", "right-line-gradient")
        .attr("gradientUnits", "userSpaceOnUse")
        .attr("x1", "0%")
        .attr("x2", "0%")
        .attr("y1", "50%")
        .attr("y2", "0%")
        .selectAll("stop")
        .data([
          { offset: "0%", color: "#B9E6FF" },
          { offset: "100%", color: "#FCB8BA" },
        ])
        .enter()
        .append("stop")
        .attr("offset", function (d) {
          return d.offset;
        })
        .attr("stop-color", function (d) {
          return d.color;
        });

      svg
        .append("svg:line")
        .attr("class", "left-axis")
        .attr("x1", width - 720)
        .attr("x2", width - 95)
        .attr("y1", height - 180)
        .attr("y2", height - 180)
        .attr("marker-end", "url(#arrow)")
        .attr("stroke", "url(#line-gradient)")
        .attr("stroke-width",function(){
          return self.isMobileScreen() ? 12 : 7
        });
      /** X label text , for X axis upper bound and lower bound*/
      svg
       .append("circle")
        .attr("cx",width - 773 )
        .attr("cy",height - 182)
        .attr("r",7)
        .attr("stroke", "#3FB0F0")
        .attr("stroke-width", "1")
        .attr("fill", "none");
      svg
      .append("circle")
        .attr("cx",width - 773 )
        .attr("cy",height - 182)
        .attr("r",4)
        .attr("stroke", "#3FB0F0")
        .attr("fill", "none")
        .attr("stroke-width", "1");
      svg
        .append("svg:text")
        .attr("x", width - 756)
        .attr("y", height - 176)
        .text("適正")
        .style("font-size", "16")
        .style("font-weight", "bold")
        .attr("fill", "#3FB0F0");

      svg
        .append("svg:text")
        .attr("x", width - 60)
        .attr("y", height - 176)
        .text("コスト大")
        .style("font-size", "16")
        .style("font-weight", "bold")
        .attr("fill", "#F67C80");

      /** y axis line */

      svg
        .append("svg:line")
        .attr("class", "right-axis")
        .attr("transform", `rotate(0)`)
        .attr("x1", -15)
        .attr("x2", -15)
        .attr("y1", height / 1.4)
        .attr("y2", height / 80 )
        .attr("marker-end", "url(#up-arrow)")
        .attr("stroke", "url(#right-line-gradient)")
        .attr("stroke-width",function(){
          return self.isMobileScreen() ? 12 : 7
        });

      svg
        .append("svg:text")
        .attr("x", -4)
        .attr("y", -2)
        .text("無駄あり")
        .style("font-size", "16")
        .style("font-weight", "bold")
        .attr("fill", "#F67C80");

      /**
       Arrow Head
      */
      const defs = svg.append("defs");
      defs
        .append("marker")
        .attr("id", "arrow")
        .attr("viewBox", "0 -5 10 10")
        .attr("refX", 0)
        .attr("refY", 0)
        .attr("markerWidth", 1)
        .attr("markerHeight", 1)
        .attr("orient", "auto")
        .append("path")
        .attr("d", "M0,-5L10,0L0,5")
        .attr("fill", "#FCB8BA");

      const rightdefs = svg.append("defs");
      rightdefs
        .append("marker")
        .attr("id", "up-arrow")
        .attr("viewBox", "0 -5 10 10")
        .attr("refX", 0)
        .attr("refY", 0)
        .attr("markerWidth", 1)
        .attr("markerHeight", 1)
        .attr("orient", "auto")
        .append("path")
        .attr("d", "M0,-5L10,0L0,5")
        .attr("fill", "#FCB8BA");

      /**  X axis legend text */
      svg
        .append("text")
        .attr("x", width / 2)
        .attr("y", height - 156)
        .attr("text-anchor", "middle")
        .text(this.$t("DASHBOARGRAPHDUPPER"))
        .style("font-weight", "700")
        .style("font-size", "16");
      svg
        .append("text")
        .attr("font-family", "FontAwesome")
        .attr("x", -23)
        .attr("y", -2)
        .attr("fill", "#F67C80")
        .style("font-size", "16")
        .text(function () {
          return "\uf071";
        });

      svg
        .append("text")
        .attr("font-family", "FontAwesome")
        .attr("x", width - 80)
        .attr("y", height - 176)
        .attr("fill", "#F67C80")
        .style("font-size", "16")
        .text(function () {
          return "\uf071";
        });

      svg
        .append("text")
        .attr("x", "-30")
        .attr("y", height / 2 - 100)
        .attr("text-anchor", "middle")
        .text("幅")
        .style("font-weight", "700")
        .style("font-size", "16");

      svg
        .append("text")
        .attr("x", "-30")
        .attr("y", height / 2 - 120)
        .attr("text-anchor", "middle")
        .text("減")
        .style("font-weight", "700")
        .style("font-size", "16");

      svg
        .append("text")
        .attr("x", "-30")
        .attr("y", height / 2 - 140)
        .attr("text-anchor", "middle")
        .text("削")
        .style("font-weight", "700")
        .style("font-size", "16");
    },
    showTreeModal() {
      this.$refs["tree-modal"].show();
    },
    onExpenseClick() {
      this.showTreeModal();
    },
  },
};
</script>

