<template>
  <div class="min-dashboard-tree-height">
    <div class="font-weight-bold">{{ treeTitle }}</div>
    <template v-if="treeData.length == 0">
      <div class="text-center errorColor">
        {{ $t("GENRALDATAERRORMESSAGE") }}
      </div>
    </template>
    <template v-else>
      <span class="tree-text">
        <b-form-checkbox
        :id="'allCheckbox'"
        v-model="selectedState"
        @change="updateAllCheckbox"
        :name="'allCheckbox'"
        class="ml-1"
        >All</b-form-checkbox>
        </span>
      <tree
        id="dashboardTree"
        ref="tree"
        :data="treeData"
        :options="treeOptions"
        @node:selected="onNodeSelected"
        @node:checked="getCheckedItem"
        @node:unchecked="getCheckedItem"
      >
        <span slot-scope="{ node }" class="tree-text">
          <template v-if="!node.hasChildren()">
            <span
              :class="
                selected_expense_item.expense_item_nm == node.text
                  ? 'selected-tree-text'
                  : ''
              "
              class="tree-text"
            >
              <span> {{ node.text }} </span>
            </span>
          </template>

          <template v-else>
            {{ node.text }}
          </template>
        </span>
      </tree>
    </template>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "SubMenuTree",
  components: {},
  inheritAttrs: false,
  props: {
    treeOptions: {
      type: [Array, Object, String, Function],
      default: () => [],
    },
    selectedState :{
      type: Boolean,
      default: () => [],
    },
    treeTitle: {
      type: String,
      default: () => [],
    },
  },
  data: () => {
    return {
      ids: [],
      checkedItems: [],
    };
  },
  computed: {
    eventsList() {
      return this.events.concat().reverse();
    },
    ...mapGetters({
      costInfoTree: "getCostInfoTreeList",
      treeData: "getTreeDashboard",
      selected_expense_item: "getSelectedExpenseItem",
      checkedItemsExpCodeArray: "getCheckedExpItemCodeArray",
    }),
  },
  mounted() {
  },
  methods: {
    updateAllCheckbox(){
      setTimeout(() => {
        console.log(this.selectedState);
        this.$emit("update-all-exp-item", this.selectedState);
        // eslint-disable-next-line no-unused-vars
        let allchecked = this.$refs.tree.findAll({
          // state: { checked: true },
        }).map((item) => {
          Object.assign(item.states, {checked: !this.selectedState})
        });
      this.getCheckedItem();
      }, 0);
    },
    getCheckedItem() {
      let checkedItems = this.$refs.tree
        .findAll({ state: { checked: true } })
        .map((item) => {
          return this._.find(this.costInfoTree, {
            expense_item_nm: item.text ? item.text : "",
          });
        });
      // checkedItems = checkedItems
      //   .filter((item) => item != null)
      //   .map((item) => item.expense_item_cd);
      let checkedDataItems = [];
      if(checkedItems.length > 0){
          checkedItems.forEach(item => {
          if(item != null){
            checkedDataItems.push({
              accounts_name_cd : item.accounts_name_cd,
              expense_item_cd : item.expense_item_cd
            })
          }
        });
      }
      this.$emit("update-multiselect-exp-item", checkedDataItems);
    },

    onNodeSelected(item) {
      this.$emit("update-expense-type", item.text);
      this.selectedExpenseItem = item.text;
    },
    dd() {
      let allchecked = this.$refs.tree.findAll({
        state: { checked: false },
      });
      return allchecked;
    },
  },
};
</script>
<style scoped lang="scss">
$primary: #062854 !default;
.selected-tree-text {
  color: blue;
}
.tree-text,
.link-text {
  font-size: 0.7rem;
  font-weight: 700;
  font: normal normal bold 12px/21px YuGothic !important;
  letter-spacing: 0px !important;
  color: #4B4B4B !important;
  opacity: 1 !important;
}
.tree-checkbox {
  border-color: #000 !important;
  height: 15px !important;
  width: 15px !important;
}
</style>
<style lang="scss" >
.tree-content {
  padding-top: 0px !important;
  padding-bottom: 0px !important;
}
.tree-checkbox.checked {
  opacity: 1;
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'%3e%3cpath fill='%23fff' d='M6.564.75l-3.59 3.612-1.538-1.55L0 4.26 2.974 7.25 8 2.193z'/%3e%3c/svg%3e") !important;
  background: no-repeat 50%/50% 50%;
}
.tree-checkbox.checked,
.tree-checkbox.indeterminate {
  background-color: #033170 !important;
}
.tree-arrow.has-child:after {
  border: none !important;
  display: none !important;
}
.tree::-webkit-scrollbar {
  width: 5px !important;
  height: 4px !important;
  border-radius: 4px !important;
  background: #fff; /* or add it to the track */
  -webkit-border-radius: 1ex;
  -webkit-box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.75);
}
</style>
