<template>
  <div>
    <b-row class="w-full">
      <b-col cols="12">
        <DashboardComponent />
      </b-col>
    </b-row>
  </div>
</template>

<script>
import DashboardComponent from "@/components/graphs/DashboardComponent";
import { ApiServicesFactory } from "@/shared/apiServiceFactory";
const BasicInfoApis = ApiServicesFactory.get("basic");
import { endPoint } from "@/shared/endPoint";

export default {
  components: { DashboardComponent },
  data() {
    return {};
  },
  mounted() {
    this.getTreeData();
  },
  methods: {
    /**
     * @description function to fetch available expenseItem for logged in clientAccount
     * @description updating to store
     * @param {int} client_no
     */
    async getTreeData() {
      const { data } = await BasicInfoApis.postAPI(
        endPoint.EVALUATIONCOSTINFOTREELIST,
        {
          client_no: localStorage.getItem("client_account_no"),
        }
      );
      if (data.success) {
        let responseData = data.data;
        this.allTreeData = responseData;
        let mapData = [];
        let groupedData = this._.groupBy(responseData, "accounts_name_nm");
        Object.keys(groupedData).forEach((key) => {
          let childrenArray = [];
          groupedData[key].forEach((item) => {
            let bindChildData = {
              text: item.expense_item_nm,
              expenseItemCode: item.expense_item_cd,
              accountsNameCode: item.accounts_name_cd,
            };
            childrenArray.push(bindChildData);
          });
          let bindTreeData = {
            text: key,
            state: { expanded: true },
            children: childrenArray,
          };
          mapData.push(bindTreeData);
        });
        this.$store.dispatch("updateTree", mapData);
        this.$store.dispatch("costInfoTreeList", this.allTreeData);
      }
    },
  },
};
</script>

<style scoped lang="scss">
.btn-custom-color {
  background-color: #062854;
  border-color: #062854;
  color: white;
}
.link-custom-color {
  color: #062854;
}
</style>
